<template>
  <div>
     <!-- Evaluate Modal -->
  <div
        class="modal fade"
        id="EvaluateModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title text-center" id="exampleModalLabel">
                Evaluate student Assignment
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <!-- modal body -->
            <div class="modal-body">
              <div class="card-body">
                <form @submit.prevent="EvaluateAssignment">
                  <div class="form-group">
                    <label for="acode">Assignment Code</label>
                    </div>
                   <div class="form-group">
                    <p>{{NewAcode}}</p>
                  </div> 
                  <div class="form-group">
                    <label for="aheading">Heading</label>
                    <p>{{NewHeading}}</p>
                  </div>
                  <div class="form-group">
                    <label for="adesc">Username</label>
                      {{NewUsername}}
                  </div>
                  <div class="form-group">
                    <label for="evaluate">Evaluate</label>
                    <input
                      type="text"
                      id="evaluate"
                      name="evaluate"
                      placeholder="Enter Marks or Grade"
                      v-model="NewMarks"
                      class="form-control"
                      required
                    />
                  </div>

                  <button class="btn btn-success mt-2 " data-bs-dismiss="modal">Submit</button>
                </form>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>


<div class="mt-4">
    <p class="fs-4 fw-bold">Select Assignment Code </p>
    <select v-model="selected" class="p-1 ms-2 me-2 pe-2 ps-2 fs-5">
        
        <option v-for="(assignment, _id) in Assignments" :key="_id" selected>{{assignment.acode}}</option>
    </select>
    <button  @click="getAssignmentsByCode" class="btn btn-success">Get Data</button>
</div>
<div class="table-responsive mt-4">
    <table class="table table-striped table-sm">
        <thead>
            <tr class="text-center fw-bold">
            <th scope="col">A Code</th>
            <th scope="col">Assignment Heading</th>
            <th scope="col">Username</th>
            <th scope="col">comments</th>
            <th scope="col">Submitted On</th>
            <th scope="col">Marks</th>
            <th scope="col">Assignment Submitted Link</th>
            <th scope="col">Evaluate</th>
            </tr>
            </thead>
            <tbody class="text-center ">
                <tr class="mt-2" v-for="(assignment, _id) in SubmittedData" :key="_id">
                <td>{{assignment.acode}}</td>
                <td>{{assignment.aheading}}</td>
                <td>{{assignment.username}}</td>
                <td>{{assignment.comments}}</td>
                <td>{{assignment.date.split("T")[0]}}</td>
                <td>{{assignment.marks}}</td>
                <td><a :href="assignment.aslink" target="_blank"><button class="btn btn-info">Open Link</button></a></td>
                <td><button class="btn btn-primary" data-bs-toggle="modal"
      data-bs-target="#EvaluateModal" @click="evaluate(_id)">Evaluate</button></td>

                
                </tr>
                
            </tbody>
    </table>
</div>



  </div>
</template>

<script>
import axios from 'axios'
import config from "@/config"

export default {
name:'AssignmentStatus',

data(){
  return{
       acode:"",
      Assignments:[],
      selected:"",
      SubmittedData:[],
      Marks:"",


      NewAcode:"",
      NewHeading:"",
      NewUsername:"",
      NewMarks:""

  }
},
methods:{

  // get assignment lists for select tag
async getAssignments(){
  
    let {
    data
  } = await axios.get(`${config.apiBaseUrl}/api/assignments/get-assignments`)
    this.Assignments = data
 

},

// Get Submitted Assignment By Acode
 async getAssignmentsByCode(){
     
        let acode = this.selected
        
         console.log(acode);
let {
    data
}= await axios.get(`${config.apiBaseUrl}/api/submissions/get-students-by-acode?acode=${acode}` )

console.log(data)
  this.SubmittedData = data
},

// update marks
evaluate(_id){
  this._id = this.SubmittedData[_id]._id
  this.NewAcode = this.SubmittedData[_id].acode
  this.NewHeading = this.SubmittedData[_id].aheading
  this.NewUsername = this.SubmittedData[_id].username
  this.NewMarks = this.SubmittedData[_id].marks
},

async EvaluateAssignment(){

  let evaluatedData = {
    _id:this._id,
    marks: this.NewMarks,
    }
  console.log(`data  ${evaluatedData}`)
    let res = await axios.patch(`${config.apiBaseUrl}/api/submissions/evaluate-marks`,evaluatedData)
      this.getAssignments() //calling get assignments
        
                this.$toasted.show('Marks updated Click Get Data again to see updated marks');

      if(res.data.success){
          console.log('add something here')
        }
        return res;


}





},
created(){
  this.getAssignments()
  // this. getAssignmentsByCode()
}
}
</script>

<style scoped>
a{
  color:white;
}
select{
  border: 1px solid #000;
    border-radius: 5px;
}

</style>